import React, { useState } from "react";
import "./manageProfile.scss";
import useUI from "../../../hooks/usefulHooks/useUI";
import Personal from "./elements/personal";
import Account from "./elements/account";
import DangerZone from "./elements/dangerZone";
import DoVerification from "./updateVerification/verifyNewChanges";
import ManagerDropdown from "./elements/managerHeader";

export default function ManageProfile() {
  const { Interface } = useUI();
  const {
    appearance: { components_background },
  } = Interface;
  const [item, setItem] = useState({
    current_section: "Personal Information",
    inputError: null,
    error: null,
    loaded: true,
  });

  function openSection(section) {
    setItem((rest) => ({ ...rest, current_section: section }));
  }

  function setError(error) {
    setItem((rest) => ({ ...rest, ...error }));
  }

  let sections = [
    {
      name: "Personal Information",
      icon: "Personal information.png",
      JSX: <Personal key="0" item={item} setItem={setError} />,
    },
    {
      name: "Account",
      icon: "account.png",
      JSX: <Account key="1" item={item} setItem={setError} />,
    },
    {
      name: "Danger-Zone",
      icon: "deleteAc.png",
      JSX: <DangerZone key="2" item={item} setItem={setError} />,
    },
  ];

  return (
    <section className="manage_profile_container">
      <ManagerDropdown
        setSection={openSection}
        backgroundClass={components_background}
        item={item}
      />

      <div id="manage_profile_body" className={components_background}>
        <div className="editable_info">
          {sections.map((e) => (e.name === item.current_section ? e.JSX : ""))}
        </div>
        <DoVerification />
      </div>
    </section>
  );
}
