import React, { useEffect } from "react";
import useUI from "../../../hooks/usefulHooks/useUI";

export default function Alert() {
  let { setInterface, Interface } = useUI();
  let {
    alert,
    appearance: { components_background },
  } = Interface;

  let setType = alert?.success
    ? ["green_alert", "success.png", alert?.message]
    : ["red_alert", "error1.png", alert?.message];

  useEffect(() => {
    let timeout = setTimeout(() => {
      setInterface({ alert: null });
    }, 2500);
    return () => clearTimeout(timeout);
  }, [alert]);

  return (
    <div
      id="Alert"
      className={`${!alert ? "hide" : ""} ${components_background} ${
        setType[0]
      }`}
    >
      <img
        src={`https://d34ct18kvr7jx3.cloudfront.net/assets/icons/${setType[1]}`
      }
        alt={setType[1]}
      />
      <span>{setType[2]}</span>
    </div>
  );
}
