import { useLocation, useNavigate } from "react-router-dom";
import usePostHook from "../httpHooks/usePostHook";
import useAuth from "../usefulHooks/useAuth";

export default function useAuthenticate() {
  const POST = usePostHook();
  const navigate = useNavigate();
  const location = useLocation();
  const { setAuth } = useAuth();
  const from = location.state?.from?.pathname || "/";

  async function DO_AUTHENTICATION(info, url) {
    const data = await POST(info, url,false);

    if (data.status === 200) {
      const { user, accessToken,socketToken } = data;
      setAuth({ user, accessToken,socketToken });
      navigate(from, { replace: true });
    }

    return data;
  }

  return DO_AUTHENTICATION;
}
