import React from "react";

export default function NotAvailable({ message, convey }) {
  let illustration = {
    noContacts: "noContact.png",
    notFound: "notFound.png",
    error: "error.png",
  };
 
  return (
    <div id="notAvailable" className="smooth-anim">
      <img
        src={`https://d34ct18kvr7jx3.cloudfront.net/assets/illustrations/${illustration[convey]}`}
        alt={illustration}
      />
      <p className="errorInfo">{message}</p>
    </div>
  );
}
