import handleResponse from "../../modules/responseHandler";
import useAxiosPrivate from "../usefulHooks/useAxiosPrivate";
import useUI from "../usefulHooks/useUI";

export default function usePatchHook() {
  const axiosPrivate = useAxiosPrivate();
  const { setInterface } = useUI();

  async function PATCH(info, url, content, showProgress = false) {
    let data = content.includes("multipart/form-data")
      ? info
      : JSON.stringify(info);

    let option = {
      headers: {
        Accept: "application/json",
        "Content-Type": content,
      },
      signal: AbortSignal.timeout(120000),
    };

    try {
      if (showProgress) {
        setInterface({ isLoaded: false });
      }

      const sendData = await axiosPrivate.patch(url, data, option);

      return handleResponse(sendData, true);
    } catch (err) {
      return handleResponse(err, false);
    }
  }

  return PATCH;
}
