import React, { useState, useEffect } from "react";
import useUI from "../../../../hooks/usefulHooks/useUI";
import ERROR from "../../../usefulComponents/informativeComponents/ERROR";
import useAuth from "../../../../hooks/usefulHooks/useAuth";
import useUpdate from "../../../../hooks/utilityHooks/useUpdate";
import useFieldChecker from "../../../../hooks/utilityHooks/fieldChecker";
import useGetHookEffect from "../../../../hooks/httpHooks/useGetHookEffect";
import useGetHook from "../../../../hooks/httpHooks/useGetHook";
import Ellipsis from "../../../usefulComponents/loadingComponents/ellipsis";
import Spinner from "../../../usefulComponents/loadingComponents/spinner";
import usePatchHook from "../../../../hooks/httpHooks/usePatchHook";

export default function Personal({ item, setItem }) {
  const PATCH = usePatchHook();
  const CHECK_FIELD = useFieldChecker();
  const UPDATED_FIELDS = useUpdate();
  const {response} = useGetHookEffect("/api/get/user/avatar");
  const GET = useGetHook();

  const { setInterface } = useUI();
  const { auth, setAuth } = useAuth();
  const { user } = auth;

  const [input, setInput] = useState({
    first_name: user.first_name,
    last_name: user.last_name,
    about: user.about,
    birthday: user.birthday,
    status: user.status,
  });

  const [getValue, setValue] = useState({
    myAvatar: user.picture,
    isDisabled: false,
    isAvatarLoaded: true,
  });

  const { first_name, last_name, about, birthday, status } = input;

  function handleChange(e) {
    const { name, value } = e.target;
    let field = { [name]: value };
    setInput((rest) => ({ ...rest, ...field }));
    let requireFields = CHECK_FIELD(UPDATED_FIELDS(field, user));
    setItem({ inputError: requireFields });
  }

  async function submitData(e) {
    e.preventDefault();
    setItem({ loaded: false, inputError: null, error: null });

    const response = await PATCH(
      input,
      "/api/user/account/manage/information",
      "application/json;charset=UTF-8",
      false
    );
    if (response.status === 200) {
      setAuth((rest) => ({ ...rest, user: response.user }));
      setItem({ loaded: true, inputError: null, error: null });
      setInterface({
        alert: { success: true, message: response.message },
      });
    } else if (
      response.status === 400 &&
      response.hasOwnProperty("inputError")
    ) {
      setItem({ inputError: response.inputError, loaded: true });
    } else {
      setItem({ loaded: true });
      setInterface({
        alert: { success: false, message: response.message },
      });
    }
    return;
  }

  async function sendFile(e) {
    e.preventDefault();
    let file = { avatar: e.target.files[0] };

    let url = "/api/user/update-avatar";
    setValue((other) => ({ ...other, isAvatarLoaded: false }));

    const response = await PATCH(file, url, "multipart/form-data", false);
    if (response.status === 200) {
      let updatedUser = { ...auth.user, picture: response.updatedAvatar };
      setAuth((rest) => ({ ...rest, user: updatedUser }));
      setValue((other) => ({ ...other, isAvatarLoaded: true }));
      setInterface({
        alert: {
          success: true,
          message: response?.message,
        },
      });
    } else {
      setValue((other) => ({ ...other, isAvatarLoaded: true }));
      setInterface({
        alert: {
          success: false,
          message: response?.message,
        },
      });
    }
  }

  useEffect(() => {
    let isMounted = true;
    if (isMounted) {
      setValue((other) => ({ ...other, myAvatar: user.picture }));
      // To update avatar immediatly
    }
    return () => {
      isMounted = false;
    };
  }, [auth]);

  useEffect(() => {
    let isMounted = true;
    if (isMounted && response) {
      if (response.status === 200) {
        setValue((other) => ({ ...other, myAvatar: response.avatar }));
      }
    }

    return () => (isMounted = false);
  }, [response]);

  async function removeAvatar(e) {
    e.preventDefault();
    setValue((other) => ({
      ...other,
      isAvatarLoaded: false,
    }));
    const data = await GET("/api/user/remove-avatar");
    if (data.status === 200) {
      let updatedUser = { ...auth.user, picture: data.user_default_avatar };
      setAuth((rest) => ({ ...rest, user: updatedUser }));
      setValue((other) => ({
        ...other,
        isAvatarLoaded: true,
      }));
    } else {
      setValue((other) => ({ ...other, isAvatarLoaded: true }));
      setInterface({
        alert: {
          success: false,
          message: data.message,
        },
      });
    }
  }

  let disabledBtn = user.picture.includes("user_default_avatar.jpg")
    ? [true, "disabled_btn"]
    : [getValue.isDisabled, ""];

  return (
    <form onSubmit={submitData} id="user_ac_form" className="user_info">
      <div className="personal_info">
        <div className="avatar_div">
          <div id="Avatar">
            <img className="avatar_img" src={getValue.myAvatar} alt="avatar" />

            {!getValue.isAvatarLoaded ? (
              <div id="avatar_loading">
                <Spinner />
              </div>
            ) : (
              ""
            )}
          </div>
          {getValue.isAvatarLoaded ? (
            <div className="avatar_btn_div">
              <label
                title="Change Avatar"
                htmlFor="update_avatar"
                className={`avatar_btn background `}
              >
                <i className="fa fa-camera" aria-hidden="true"></i>
              </label>

              <button
                type="button"
                title="Remove Avatar"
                disabled={disabledBtn[0]}
                className={`avatar_btn background ${disabledBtn[1]}`}
                onClick={removeAvatar}
              >
                <i className="fas fa-trash-alt"></i>
              </button>

              <a
                title="View Avatar"
                id="view_avatar"
                href={getValue.myAvatar}
                target="_blank"
                rel="noopener noreferrer"
                className={`avatar_btn background`}
              >
                <i className="fas fa-expand-alt"></i>
              </a>

              <input
                id="update_avatar"
                onChange={sendFile}
                type="file"
                accept="image/*"
                name="avatar"
              />
            </div>
          ) : (
            ""
          )}
        </div>

        <div className="first_info">
          <div className="cells_header">
            <h3>Personal Information</h3>
            <i className="fas fa-user-edit"></i>
          </div>
          <div className="preferences-inputs">
            <div className="input_wrapper">
              <label htmlFor="first_name_edit">
                <input
                  id="first_name_edit"
                  type="input"
                  name="first_name"
                  maxLength={20}
                  placeholder="First Name"
                  value={first_name}
                  onChange={handleChange}
                  aria-describedby="addon-wrapping"
                />
                <span className="spanPlaceholder">First name</span>
              </label>
              <ERROR error={item.inputError?.first_name} />
            </div>
            <div className="input_wrapper">
              <label htmlFor="last_name_edit">
                <input
                  id="last_name_edit"
                  type="input"
                  maxLength={20}
                  name="last_name"
                  placeholder="Last Name"
                  value={last_name}
                  onChange={handleChange}
                  aria-describedby="addon-wrapping"
                />{" "}
                <span className="spanPlaceholder">Last name</span>
              </label>
              <ERROR error={item.inputError?.last_name} />
            </div>

            <div className="input_wrapper">
              <label htmlFor="birthday">
                <input
                  id="birthday"
                  name="birthday"
                  value={birthday}
                  onChange={handleChange}
                  placeholder="Birthday"
                  type="date"
                  min="1900-01-01"
                  max="2050-12-31"
                  required
                />
                <span className="birthdayPlaceholder">Birthday</span>
              </label>
              <ERROR error={item.inputError?.birthday} />
            </div>

            <div className="input_wrapper ">
              <label htmlFor="status_edit">
                <input
                  id="status_edit"
                  type="input"
                  name="status"
                  maxLength={20}
                  placeholder="Status"
                  value={status}
                  onChange={handleChange}
                  aria-describedby="addon-wrapping"
                />
                <span className="spanPlaceholder">Status</span>
              </label>

              <ERROR error={item.inputError?.status} />
            </div>
          </div>
        </div>
        <div className="about">
          <label htmlFor="about_edit">About</label>
          <textarea
            id="about_edit"
            value={about}
            onChange={handleChange}
            rows="3"
            placeholder={about}
            name="about"
          ></textarea>
        </div>
      </div>

      {Object.keys(UPDATED_FIELDS(input, user)).length !== 0 ? (
        <div className="profile_save_button">
          {!item.loaded ? (
            <Ellipsis />
          ) : (
            <input type="submit" value="Save changes" className="myBtn" />
          )}
        </div>
      ) : (
        ""
      )}
    </form>
  );
}
