import { useState } from "react";
import "../../../App.scss";
import { Link } from "react-router-dom";
import useAuthenticate from "../../../hooks/componentHooks/authenticate";
import VerifyUser from "../verification/VerifyUser";
import ERROR from "../../usefulComponents/informativeComponents/ERROR";
import Ellipsis from "../../usefulComponents/loadingComponents/ellipsis";
import AnimBackground from "../../usefulComponents/otherComponents/svgBackground";

export default function Signup() {
  const AUTHENTICATE = useAuthenticate();
  const [item, setItem] = useState({
    email: "",
    password: "",
    confirm_password: "",
    username: "",
    first_name: "",
    last_name: "",
    birthday: "",
    inputError: null,
    otherError: null,
    isLoaded: true,
    verifyNow: false,
  });

  const {
    email,
    password,
    username,
    first_name,
    last_name,
    confirm_password,
    isLoaded,
    birthday,
    inputError,
    otherError,
    verifyNow,
  } = item;

  function changeState(changes) {
    setItem((rest) => ({ ...rest, ...changes }));
    return;
  }

  function handleChange(e) {
    const { value, name } = e.target;
    changeState({ [name]: value, inputError: null, otherError: null });
  }

  async function submitData(e) {
    e.preventDefault();
    changeState({ isLoaded: false, inputError: null, otherError: null });
    const isSignup = await AUTHENTICATE(
      {
        email,
        password,
        username,
        first_name,
        last_name,
        confirm_password,
        birthday,
      },
      "/signup"
    );

    if (isSignup.status === 202) {
      changeState({ verifyNow: true, isLoaded: true });
    } else if (isSignup?.status === 400) {
      changeState({
        inputError: isSignup?.inputError,
        isLoaded: true,
      });
    } else {
      changeState({
        otherError: {
          message: isSignup?.message,
          status: isSignup?.status,
        },
        isLoaded: true,
      });
    }
  }

 
 
  return (
    <div className="auth-container">
      <div id="signup" className="auth-box glass layers-anim">
        <div className="authHeader">
          <div id="appNameAndLogo">
            <img id="app_logo" src="https://d34ct18kvr7jx3.cloudfront.net/assets/logo/strrings-logo-96px.png" alt="logo" />
            <h2>Strrings</h2>
          </div>
          <p>Create your Strrings Account</p>
        </div>

        <div className="authInputBox">
          <form className="auth-form" onSubmit={submitData}>
            <div className="textInputs">
              <div className="firstAndLast_name">
                <div className="input_wrapper">
                  <label htmlFor="first_name">
                    <input
                      type="text"
                      id="first_name"
                      name="first_name"
                      className={inputError?.first_name ? "input_error" : ""}
                      value={first_name}
                      onChange={handleChange}
                      placeholder="First name"
                      maxLength="30"
                      required
                    />
                    <span className="spanPlaceholder">First name</span>
                  </label>
                  <ERROR error={inputError?.first_name} />
                </div>

                <div className="input_wrapper">
                  <label htmlFor="last_name">
                    <input
                      type="text"
                      id="last_name"
                      name="last_name"
                      className={inputError?.last_name ? "input_error" : ""}
                      value={last_name}
                      onChange={handleChange}
                      placeholder="Last name"
                      maxLength="30"
                    />
                    <span className="spanPlaceholder">Last name</span>
                  </label>
                  <ERROR error={inputError?.last_name} />
                </div>
              </div>
              <div className="input_wrapper">
                <label htmlFor="username">
                  <input
                    type="text"
                    id="username"
                    name="username"
                    className={inputError?.username ? "input_error" : ""}
                    value={username}
                    onChange={handleChange}
                    placeholder="Username"
                    maxLength="30"
                    required
                  />
                  <span className="spanPlaceholder">Username</span>
                </label>
                <ERROR error={inputError?.username} />
              </div>

              <div className="input_wrapper">
                <label htmlFor="email">
                  <input
                    type="email"
                    id="email"
                    name="email"
                    className={inputError?.email ? "input_error" : ""}
                    value={email}
                    onChange={handleChange}
                    placeholder="Email"
                    required
                  />
                  <span className="spanPlaceholder">Email</span>
                </label>
                <ERROR error={inputError?.email} />
              </div>
              <div className="input_wrapper">
                <label htmlFor="password">
                  <input
                    type="password"
                    id="password"
                    name="password"
                    className={inputError?.password ? "input_error" : ""}
                    value={password}
                    onChange={handleChange}
                    placeholder="Password"
                    required
                  />
                  <span className="spanPlaceholder">Password</span>
                </label>
                <ERROR error={inputError?.password} />
              </div>

              <div className="input_wrapper">
                <label htmlFor="confirm_password">
                  <input
                    type="password"
                    id="confirm_password"
                    className={
                      inputError?.confirm_password ? "input_error" : ""
                    }
                    name="confirm_password"
                    value={confirm_password}
                    onChange={handleChange}
                    placeholder="Confirm Password"
                    required
                  />
                  <span className="spanPlaceholder">Confirm Password</span>
                </label>
                <ERROR error={inputError?.confirm_password} />
              </div>
              <div className="input_wrapper">
                <label htmlFor="birthday">
                  <input
                    id="birthday"
                    name="birthday"
                    className={inputError?.birthday ? "input_error" : ""}
                    value={birthday}
                    onChange={handleChange}
                    placeholder="Birthday"
                    type="date"
                    min="1900-01-01"
                    max="2050-12-31"
                    required
                  />
                  <span className="birthdayPlaceholder">Birthday</span>
                </label>
                <ERROR error={inputError?.birthday} />
              </div>
            </div>
            <ERROR error={otherError} />
            <div className="inputsBtn">
              <div id="signin_Btn" className="next">
                {isLoaded ? (
                  <p>
                    Already have an account?{" "}
                    <Link to="/signin" replace>
                      Login
                    </Link>
                  </p>
                ) : (
                  ""
                )}
              </div>

              <div className="next">
                {!isLoaded ? (
                  <Ellipsis />
                ) : (
                  <input
                    id="signupBtn"
                    className="continueBtn"
                    type="submit"
                    value="Continue"
                  />
                )}
              </div>
            </div>
          </form>
        </div>
      </div>
      <VerifyUser
        verificationType={"signupVerification"}
        verifyNow={verifyNow}
        cancelVerification={setItem}
      />
      <AnimBackground
        color0={"#322DED"}
        color1={"#B537F0"}
        color2={"#F241EF"}
        color3={"#3C37F0"}
        color4={"#F2669B"}
      />
    </div>
  );
}
