import { useState } from "react";
import useUI from "../../../../hooks/usefulHooks/useUI";
import ERROR from "../../../usefulComponents/informativeComponents/ERROR";
import useAuth from "../../../../hooks/usefulHooks/useAuth";
import useUpdate from "../../../../hooks/utilityHooks/useUpdate";
import useFieldChecker from "../../../../hooks/utilityHooks/fieldChecker";
import usePatchHook from "../../../../hooks/httpHooks/usePatchHook";
import Ellipsis from "../../../usefulComponents/loadingComponents/ellipsis";
import Info from "../../../usefulComponents/informativeComponents/INFO";

export default function Account({ item, setItem }) {
  const PATCH = usePatchHook();
  const CHECK_FIELD = useFieldChecker();
  const UPDATED_FIELDS = useUpdate();
  const { setInterface } = useUI();
  const {
    setAuth,
    auth: { user },
  } = useAuth();

  const [input, setInput] = useState({
    username: user.username,
    email: user.email,
  });

  const [disable, setDisable] = useState(false);

  function handleChange(e) {
    const { name, value } = e.target;
    let field = { [name]: value };
    setInput((rest) => ({ ...rest, ...field }));
    let requireFields = CHECK_FIELD(UPDATED_FIELDS(field, user));
    setItem({ inputError: requireFields });
  }

  async function handleSubmit(e) {
    e.preventDefault();
    setItem({ loaded: false, inputError: null, error: null });

    const response = await PATCH(
      UPDATED_FIELDS(input, user), // only send updated fields to server
      "/api/user/account/manage/unique/email&username",
      "application/json;charset=UTF-8",
      false
    );
    if (response.status === 200) {
      setInterface({
        alert: { success: true, message: response?.message },
      });
      setItem({ loaded: true, inputError: null, error: null });
      setAuth((rest) => ({
        ...rest,
        user: { ...user, ...response.updated },
      }));
     
    } else if (response.status === 202) {
      setItem({ loaded: true, inputError: null, error: null });
      setInterface({ isVerified: false });
      setAuth((rest) => ({
        ...rest,
        user: { ...user, ...response.updated },
      }));
    } else if (response.status === 400) {
      setItem({ loaded: true, inputError: response.inputError, error: null });
    } else {
      setItem({ loaded: true, inputError: null });
      setInterface({
        alert: { success: false, message: response.message },
      });
    }

    return setDisable(false);
  }
  return (
    <>
      <form onSubmit={handleSubmit} className="user_info">
        <div className="inputs-div">
          <div className="cells_header">
            <h3>Account</h3>
            <i className="fas fa-user-circle"></i>
          </div>
          <div className="input_wrapper">
            <label htmlFor="status_edit">
              <input
                type="text"
                id="edit_username"
                name="username"
                placeholder="Username"
                value={input.username}
                onChange={handleChange}
                disabled={disable}
              />
              <span className="spanPlaceholder">Username</span>
            </label>
            <ERROR error={item?.inputError?.username} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="status_edit">
              <input
                id="edit_email"
                type="email"
                name="email"
                placeholder="Email"
                value={input.email}
                onChange={handleChange}
                disabled={disable}
              />
              <span className="spanPlaceholder">Email ID</span>
            </label>
            <ERROR error={item?.inputError?.email} />
          </div>
          {UPDATED_FIELDS(input, user).hasOwnProperty("email") ? (
            <Info info="A verification code will be sent to this email address." />
          ) : (
            ""
          )}

          {Object.keys(UPDATED_FIELDS(input, user)).length ? (
            <div className="profile_save_button">
              {!item.loaded ? (
                <Ellipsis />
              ) : (
                <input type="submit" value="Save changes" className="myBtn" />
              )}
            </div>
          ) : (
            ""
          )}
        </div>
      </form>
    </>
  );
}
